import React from 'react';
import { Formik, Field, Form } from 'formik';
import * as yup from 'yup';
// import { useDispatch, useSelector } from 'react-redux';

import { PersistFormikValues } from 'formik-persist-values';
import { Btn } from 'components/Btn/Btn';

import styles from './DailyCaloriesForm.module.scss';

export function DailyCaloriesForm() {
  // const dispatch = useDispatch();

  const handleSubmit = (values, userId) => {
    //   if (userId) {
    //     dispatch(dailyCaloriesAuth(values, userId));
    //   } else {
    //     dispatch(dailyCalories(values));
    //   }
  };
  const isChecked = () => {
    const store = localStorage.getItem('bloodType');
    if (store) {
      return store;
    }
    return '';
  };
  const validationsSchema = yup.object().shape({
    height: yup
      .number()
      .typeError('  ')
      .min(100, '  100 ')
      .max(220, '  220 ')
      .required("' "),
    age: yup
      .number()
      .typeError('  ')
      .min(18, '  18 ')
      .max(90, '  90 ')
      .required("' "),
    currentWeight: yup
      .number()
      .typeError('  ')
      .min(45, '  45 ')
      .max(200, '  200 ')
      .required("' "),
    desiredWeight: yup
      .number()
      .typeError('  ')
      .min(40, '  40 ')
      .max(
        yup.ref('currentWeight'),
        '      '
      )
      .required("' "),
    bloodType: yup.number().required(' '),
  });

  return (
    <>
      <div>
        <Formik
          initialValues={{
            height: '',
            age: '',
            currentWeight: '',
            desiredWeight: '',
            bloodType: `${isChecked()}`,
          }}
          validateOnBlur
          onSubmit={(values, actions) => {
            handleSubmit(values);
            // handleSubmit(values, userId);
            // if (IsAuthenticated) {
            //   localStorage.removeItem('bloodType');
            //   localStorage.removeItem('calc-form');
            //   actions.resetForm();
            // }
          }}
          validationSchema={validationsSchema}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            isValid,
            handleSubmit,
            dirty,
          }) => (
            <Form className={styles.caloriesForm} onSubmit={handleSubmit}>
              <h1>      </h1>
              <div className={styles.formContainerMain}>
                <div className={styles.formContainerLeft}>
                  <div className={styles.labelContainer}>
                    <InputField
                      label=" *"
                      type="number"
                      name={'height'}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.height}
                    />
                    <div className={styles.caloriesFormErrorContainer}>
                      {touched.height && errors.height && (
                        <p className={styles.caloriesFormError}>
                          {errors.height}
                        </p>
                      )}
                    </div>
                  </div>
                  <div className={styles.labelContainer}>
                    <InputField
                      label=" *"
                      type="number"
                      name={'age'}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.age}
                    />
                    <div className={styles.caloriesFormErrorContainer}>
                      {touched.age && errors.age && (
                        <p className={styles.caloriesFormError}>{errors.age}</p>
                      )}
                    </div>
                  </div>
                  <div className={styles.labelContainer}>
                    <InputField
                      label="  *"
                      type="number"
                      name={'currentWeight'}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.currentWeight}
                    />
                    <div className={styles.caloriesFormErrorContainer}>
                      {touched.currentWeight && errors.currentWeight && (
                        <p className={styles.caloriesFormError}>
                          {errors.currentWeight}
                        </p>
                      )}
                    </div>
                  </div>
                </div>
                <div className={styles.formContainerRight}>
                  <div className={styles.labelContainer}>
                    <InputField
                      label="  *"
                      type="number"
                      name={'desiredWeight'}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.desiredWeight}
                    />
                    <div className={styles.caloriesFormErrorContainer}>
                      {touched.desiredWeight && errors.desiredWeight && (
                        <p className={styles.caloriesFormError}>
                          {errors.desiredWeight}
                        </p>
                      )}
                    </div>
                  </div>
                  <div className={styles.radioButtonContainer}>
                    <h3>  *</h3>

                    <ul className={styles.radioButtonList}>
                      <RadioButton
                        onChange={handleChange}
                        onBlur={handleBlur}
                        name="bloodType"
                        value="1"
                        id="1-radio-button"
                      />
                      <RadioButton
                        onChange={handleChange}
                        onBlur={handleBlur}
                        name="bloodType"
                        value="2"
                        id="2-radio-button"
                      />
                      <RadioButton
                        onChange={handleChange}
                        onBlur={handleBlur}
                        name="bloodType"
                        value="3"
                        id="3-radio-button"
                      />
                      <RadioButton
                        onChange={handleChange}
                        onBlur={handleBlur}
                        name="bloodType"
                        value="4"
                        id="4-radio-button"
                      />
                    </ul>
                    <div className={styles.caloriesFormErrorContainer}>
                      {touched.bloodType && errors.bloodType && (
                        <p className={styles.caloriesFormError}>
                          {errors.bloodType}
                        </p>
                      )}
                    </div>
                  </div>
                </div>
              </div>

              <div className={styles.form_button}>
                {/* <Button
                  disabled={!isValid || !dirty}
                  onClick={handleSubmit}
                  type="submit"
                  customType="primary"
                /> */}
                <Btn onClick={handleSubmit} type="submit">
                   
                </Btn>
              </div>

              <PersistFormikValues name="calc-form" ignoreValues="bloodType" />
            </Form>
          )}
        </Formik>
      </div>
    </>
  );
}
const InputField = ({ label, type, value, name, onChange, onBlur }) => (
  <label>
    <Field
      required
      type={type}
      value={value}
      name={name}
      onChange={onChange}
      onBlur={onBlur}
    />
    <div className={styles.labelText}>{label}</div>
  </label>
);

const RadioButton = ({ name, value, id, onChange, onBlur }) => (
  <li>
    <Field
      type="radio"
      value={value}
      name={name}
      id={id}
      onChange={onChange}
      onBlur={onBlur}
    />
    <label htmlFor={id}>{value}</label>
    <div className={styles.check}>
      <div className={styles.inside}></div>
    </div>
  </li>
);
